@import "../utils/mixins.scss";

.home {
  &__title {
    height: 100vh;
    background: url(../../images/homepage-background.jpeg) no-repeat
      center/cover;
    color: #fff;
    text-align: center;
    margin-bottom: 150px;

    &-text {
      font-size: 32px;
      padding-top: 20%;

      @include onTablet {
        font-size: 5vw;
        padding-top: 10vh;
      }
    }

    &-brand {
      color: #00b4d8;
      padding-top: 35px;
      font-size: 66px;
    }
  }

  &__about {
    margin-bottom: 50px;

    &-desc {
      display: flex;
      justify-content: space-around;
      // width: 100%;
      margin-bottom: 50px;


      @include onTablet {
        flex-direction: row;
        gap: 24px;
      }

      &__title {
        color: #00b4d8;
        font-size: 32px;
        font-weight: 600;

        @include onDesktop {
          max-width: 550px;
          font-size: 64px;
        }
      }

      &__text {
        @include onTablet {
          max-width: 500px;
        }
      }
    }

    &__list {
      display: flex;
      justify-content: space-around;
      width: 100%;

      &-featch {
        color: #00b4d8;
        font-size: 62px;
      }
    }
  }
}
